.reports__table {
  width: 100%;
  height: auto;
  border-collapse: collapse;
  /* border-radius: 10px; */
}

.reportsTable__header {
  color: var(--blue-color);
  /* width: 80%; */
  font-weight: bold;
  padding: 10px 0px;
}
.reports__table__data {
  padding: 10px 10px;
  /* text-align: left; */
  color: white;
}

.reports__table tr:nth-child(odd) {
  background: var(--table-header-background);
}

.reports__table__container {
  width: 90vw;
  margin: auto;
  padding: 20px;
  overflow-x: scroll;
}
.reports__table tr td:first-of-type {
  text-align: left;
  padding-left: 20px;
}

@media (max-width: 425px) {
  .reports__table__container {
    padding: 0px;
  }
  .reports__table tr td:first-of-type {
    position: sticky;
    left: 0;
    min-width: 200px;
    text-align: left;
    padding-left: 5px;
    font-weight: bold;
  }
}
