.competetor-page-container {
  width: 93vw;
  display: grid;
  grid-template-columns: 30vw 30vw 30vw;
  margin: auto;
  padding: 20px;
}

.main-card-contain {
  display: flex;
  flex-direction: column;
  /* width: 300px; */
  height: 190px;
  margin: 10px;
  box-shadow: 0 4px 6px 0;
  border-radius: 8px;
}
.com-name {
  color: var(--blue-color);
  font-size: 24px;
  margin: 35px 146px 0px 0px;
  text-align: left;
}

.company-tickers {
  color: var(--blue-color);
  text-align: left;
  margin-top: 25px;
}
.logo-image {
  float: left;
  margin: 10px;
}

.main-card-contain .flex-company-name {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
  line-height: 1px;
}
.flex-company-name .company-logo {
  flex-grow: 0;
  height: fit-content;
}
.flex-company-name .company-name {
  flex-grow: 4;
}

.main-card-contain .flex-company-values {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
}

.flex-company-values .company-stats {
  flex-grow: 4;
  line-height: 1px;
}
.company-stats .comp-stock-price {
  color: white;
  font-size: 25px;
  /* text-align: left; */
  /* margin-left: 10px; */
}
.company-stats .price-on-date {
  color: white;
  font-size: 12px;
  margin: 0px 114px 0px 0px;
}
.flex-company-values .compe- {
  flex-grow: 2;
}
.company-unfollow .unfollow-button {
  width: 70px;
  height: 30px;
  margin-top: 20px;
  border: 1px solid var(--pink-color);
  outline: none;
  background-color: var(--card-background);
  border-radius: 4px;
  color: var(--pink-color);
}

.company-unfollow .unfollow-button:active {
  width: 70px;
  height: 30px;
  margin-top: 20px;
  border: 1px solid var(--pink-color);
  outline: none;
  background-color: var(--pink-color);
  border-radius: 4px;
  color: #ff3366;
}

.company-unfollow .unfollow-button:hover {
  color: #fff;
  background: #ff3366;
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .competetor-page-container {
    width: 93vw;
    display: grid;
    grid-template-columns: 45vw 45vw;
    margin: auto;
    padding: 20px;
  }

  .com-name {
    color: var(--blue-color);
    font-size: 25px;
    margin: 35px 146px 0px 0px;
    text-align: left;
  }

  .company-tickers {
    color: var(--blue-color);
    text-align: left;
  }
  .logo-image {
    float: left;
    margin: 10px;
  }
}

@media only screen and (max-width: 425px) {
  .competetor-page-container {
    /* width: 98vw; */
    display: grid;
    grid-template-columns: 90vw;
    margin: auto;
    padding: 20px;
  }
}
