.user-profile-navigation-wrp {
  display: block;
  height: 100%;
  width: 300px;
  box-shadow: 0px 4px 8px 0px;
  background-color: var(--card-background);
  padding-top: 2px;
  transform-origin: 0% 0%;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  z-index: 2;
}

.loader-for-user-prof {
  position: absolute;
  top: 100px;
  left: 700px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.check-box-for-hamberger,
.show-hamberger-button {
  display: none;
}
.loader-wrapper {
  position: absolute;
  top: -50px;
  left: 600px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.load-progress-bar {
  width: 500px;
}

.success-or-error-msg {
  color: var(--green-color);
  position: absolute;
  top: 260px;
  left: 730px;
}
.success-or-error-msg1 {
  color: var(--red-color);
  position: absolute;
  top: 260px;
  left: 730px;
}
.download-format-button {
  margin: 10px 320px;
  width: 200px;
  height: 40px;
  border-radius: 4px;
  color: white;
  background: #3399ff;
  border: none;
}
.input-tp-upload-file {
  position: absolute;
  top: 300px;
  left: 550px;
  height: 300px;
  width: 500px;
  border: 4px white dashed;
  background-color: #33e1ff;
  opacity: 12%;
  box-shadow: 0px 2px 4px;
  border-radius: 4px;
}
.upload-label {
  position: absolute;
  top: 570px;
  left: 700px;
  color: grey;
}
.input-tp-upload-file::-webkit-file-upload-button {
  visibility: hidden;
  color: grey;
}

.upload-icon {
  color: grey;
  height: 200px;
  width: 200px;
  position: absolute;
  top: -50px;
  left: 700px;
  font-size: 300px;
  z-index: -2;
  font-weight: 1px;
}

.user-logo-name-container {
  align-items: center;
}
.user-logo-name-container .img-for-user-prof {
  margin: 10px;
  width: 130px;
  height: 130px;
  border-radius: 50%;
}

.profile-nav-options-align {
  width: 30px;
  height: 30px;
  margin: 2px 7px;
}
.navigation-option-container {
  color: var(--blue-color);
  float: left;
  font-size: 20px;
  text-align: justify;
}
.head-search-bar {
  grid-area: search-bar;
}
.mid-company-cards {
  grid-area: body-card;
}
.main-user-profile-wrapper {
  display: grid;
  grid-template-areas: "head body-card body-card ";
  /* "head body-card body-card "; */
  height: 100vh;
  grid-gap: 10px;
  padding: 2px;
}
.empty-text-div {
  margin-top: 0px;
}
.empty-text-ex {
  color: var(--blue-color);
  font-size: 25px;
  position: unset;
}

