* {
  scroll-behavior: smooth;
  font-family: "Lato", sans-serif;
  text-decoration: none;
  box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
*:focus {
  outline: 0;
}
::-webkit-scrollbar {
  display: none;
}
:root {
  --indexes-bar-background: #1f2435;
  --green-color: #0eff00;
  --blue-color: #15ddd6;
  --primary-navbar-background: #1c2030;
  --pink-color: #ff3366;
  --red-color: #ff1000;
  --card-background: #1c2030;
  --table-header-background: #252937;
  --footer-background: #1c2030;
  --page-background: #1f2435;
  --blue-navbar-background: #3399ff;
}
body {
  margin: 0px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: var(--page-background); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.dropdown-container {
  /* float: right; */
  /* margin: 0 10px; */
  margin-left: 80vw;
  position: relative;
  display: flex;
  flex-direction: row;
}
.dropdown__parent {
  display: flex;
  width: 100%;
  margin-top: 40px;
}
.reports-dropdown {
  border-radius: 8px;
  width: 100px;
  cursor: pointer;
  padding: 5px 20px;
  color: var(--pink-color);
  border: 1px solid var(--pink-color);
}
i {
  border: solid var(--pink-color);
  border-width: 0 3px 3px 0;
  display: inline-block;
  /* margin-top: calc(100% - 10px) 50%; */
  margin: 3px 4px;
  padding: 3px;
}
.droparrow {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.reports-dropdown:active,
.reports-dropdown:focus {
  outline: none;
}
/* .reports-dropdown:hover {
  background: var(--pink-color);
  color: white;
} */

.dropdown-container:hover .reports-year-option {
  display: block;
  margin-left: 80vw;
}
.reports-year-option ul {
  padding: 0px;
}
.reports-year-option li {
  list-style-type: none;
  padding: 3px 2px;
  text-align: center;
}
.showDisplay {
  display: block;
  width: 100px;
  border: 1px solid var(--table-header-background);
  background: var(--table-header-background);
  color: var(--pink-color);
  right: 0px;
  top: 35px;
}
.showDisplay li {
  list-style-type: none;
  padding: 3px 2px;
  text-align: center;
}
.showDisplay ul {
  padding: 0px;
}
.noDisplay {
  display: none;
}
.reports-year-option {
  display: none;
  width: 100px;
  border: 1px solid var(--table-header-background);
  background: var(--table-header-background);
  color: var(--pink-color);
}
.Reports {
  height: 600px;
  overflow-y: scroll;
  margin-top: 20px;
}
.Reports h2 {
  color: var(--pink-color);
  text-align: left;
  margin-top: 20px;
  margin-left: 7vw;
  margin-bottom: 0px;
  font-size: 40px;
}

@media (max-width: 425px) {
  .Reports h2 {
    font-size: 30px;
    margin-bottom: 5px;
    margin-left: 5vw;
  }
  .dropdown-container {
    margin-left: 60vw;
  }
}

.reports__table {
  width: 100%;
  height: auto;
  border-collapse: collapse;
  /* border-radius: 10px; */
}

.reportsTable__header {
  color: var(--blue-color);
  /* width: 80%; */
  font-weight: bold;
  padding: 10px 0px;
}
.reports__table__data {
  padding: 10px 10px;
  /* text-align: left; */
  color: white;
}

.reports__table tr:nth-child(odd) {
  background: var(--table-header-background);
}

.reports__table__container {
  width: 90vw;
  margin: auto;
  padding: 20px;
  overflow-x: scroll;
}
.reports__table tr td:first-of-type {
  text-align: left;
  padding-left: 20px;
}

@media (max-width: 425px) {
  .reports__table__container {
    padding: 0px;
  }
  .reports__table tr td:first-of-type {
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    min-width: 200px;
    text-align: left;
    padding-left: 5px;
    font-weight: bold;
  }
}

.competetor-page-container {
  width: 93vw;
  display: grid;
  grid-template-columns: 30vw 30vw 30vw;
  margin: auto;
  padding: 20px;
}

.main-card-contain {
  display: flex;
  flex-direction: column;
  /* width: 300px; */
  height: 190px;
  margin: 10px;
  box-shadow: 0 4px 6px 0;
  border-radius: 8px;
}
.com-name {
  color: var(--blue-color);
  font-size: 24px;
  margin: 35px 146px 0px 0px;
  text-align: left;
}

.company-tickers {
  color: var(--blue-color);
  text-align: left;
  margin-top: 25px;
}
.logo-image {
  float: left;
  margin: 10px;
}

.main-card-contain .flex-company-name {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
  line-height: 1px;
}
.flex-company-name .company-logo {
  flex-grow: 0;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.flex-company-name .company-name {
  flex-grow: 4;
}

.main-card-contain .flex-company-values {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
}

.flex-company-values .company-stats {
  flex-grow: 4;
  line-height: 1px;
}
.company-stats .comp-stock-price {
  color: white;
  font-size: 25px;
  /* text-align: left; */
  /* margin-left: 10px; */
}
.company-stats .price-on-date {
  color: white;
  font-size: 12px;
  margin: 0px 114px 0px 0px;
}
.flex-company-values .compe- {
  flex-grow: 2;
}
.company-unfollow .unfollow-button {
  width: 70px;
  height: 30px;
  margin-top: 20px;
  border: 1px solid var(--pink-color);
  outline: none;
  background-color: var(--card-background);
  border-radius: 4px;
  color: var(--pink-color);
}

.company-unfollow .unfollow-button:active {
  width: 70px;
  height: 30px;
  margin-top: 20px;
  border: 1px solid var(--pink-color);
  outline: none;
  background-color: var(--pink-color);
  border-radius: 4px;
  color: #ff3366;
}

.company-unfollow .unfollow-button:hover {
  color: #fff;
  background: #ff3366;
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .competetor-page-container {
    width: 93vw;
    display: grid;
    grid-template-columns: 45vw 45vw;
    margin: auto;
    padding: 20px;
  }

  .com-name {
    color: var(--blue-color);
    font-size: 25px;
    margin: 35px 146px 0px 0px;
    text-align: left;
  }

  .company-tickers {
    color: var(--blue-color);
    text-align: left;
  }
  .logo-image {
    float: left;
    margin: 10px;
  }
}

@media only screen and (max-width: 425px) {
  .competetor-page-container {
    /* width: 98vw; */
    display: grid;
    grid-template-columns: 90vw;
