.wrapper__tooltip {
  right: 0;
  position: absolute;
}
.tooltip__reusable {
  position: relative;
  display: inline-block;
}

.tooltip__reusable .tooltiptext__reusable {
  position: absolute;
  margin-right: 10px;
  /* max-width: 420px; */
  width: 200px;
  text-align: left;
  display: block;
  overflow-wrap: break-word;
  visibility: hidden;
  background-color: var(--pink-color);
  opacity: 1;
  color: #fff;
  height: auto;
  border-radius: 6px;
  padding: 5px 5px;
  z-index: 1;
  top: -5px;
  right: 105%;
}

.tooltip__reusable:hover .tooltiptext__reusable {
  visibility: visible;
}

