.stock-page-secondary-navbar {
  background-color: #1f2435;
  color: #15ddd6;
  display: grid;
  grid-template-rows: 230px 45px;
  grid-template-columns: 40% 58%;
}

/* STOCKS IMGAGE COMPANY NAME star ratings */

.ticker__img-name-index-stars-wrapper {
  grid-row: 1/2;
  grid-column: 1/2;
  display: flex;
  margin: 20px 0px 0px 20px;
}
.ticker-img-name-container {
  display: flex;
}
#stock-company-img {
  height: 70px;
  width: 70px;
}
.ticker-img-name-container-2 {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 0px 0px 0px 18px;
}
#ticker-name {
  font-size: 35px;
  font-weight: 400;
  margin: 0;
}
#stock-secnav-index-name {
  font-size: 15px;
  margin: 5px 0px 0px 0px;
}
#stock-secnav-closing-price {
  font-size: 30px;
  margin-top: 18px;
  color: #ffffff;
}
#date {
  color: white;
  word-spacing: 2px;
  margin-top: 2px;
  font-size: 18px;
  opacity: 0.9;
}
.star-ratings {
  margin: 5px 10px 0px 10px;
}
/* navigation links */
.stock-page-nav-links-div {
  grid-row: 1/3;
  grid-column: 1/2;
  align-items: baseline;
  margin-top: 210px;
}
.stock-nav-tabs > li {
  float: left;
  font-size: 20px;
  margin-left: 35px;
  list-style-type: none;
}
a.active-links {
  margin-left: 0;
  border-bottom: 2px solid #15ddd6;
  padding-bottom: 10px;
  color: white;
  text-decoration: none;
}
a.not-active {
  padding-bottom: 10px;
  color: #15ddd6;
  text-decoration: none;
}

.stock-nav-tabs > li > a:hover {
  border-bottom: 2px solid #15ddd6;
  padding-bottom: 10px;
  color: white;
  cursor: pointer;
}
/* STOCK TICKER INFO */
.stock-ticker-info {
  grid-row: 1/2;
  grid-column: 2/3;
  text-align: left;
}
.stock-company-info-container {
  display: flex;
  justify-content: space-evenly;
  margin: 23px 10px;
}
.single-info-div {
  margin: 0px 20px;
  font-size: 18px;
}

/* Button div */
.follow-button-div {
  margin-top: 70px;
  width: 93%;
}
#follow-btn {
  color: white;
  background-color: #ff3366;
  border: 1px solid #ff3366;
  border-radius: 4px;
  font-size: 18px;
  width: 180px;
  padding: 6px;
  float: right;
  cursor: pointer;
}
#unFollow-btn {
  color: #ff3366;
  background-color: transparent;
  border: 1px solid #ff3366;
  border-radius: 4px;
  font-size: 18px;
  width: 180px;
  padding: 6px;
  float: right;
  cursor: pointer;
}
#follow-btn:focus {
  outline: 0;
}
#unFollow-btn:focus {
  outline: 0;
}
