.navbar__container {
  width: 100%;
  display: block;
  background-color: #1c2030;
  padding: 0 15px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
}
.navbar__flex {
  position: relative;
  width: 100%;
  max-width: 1140px;
  min-height: 75px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__left,
.navbar__right {
  height: 50px;
}
.navbar__hamburger {
  display: inline-block;
  width: 100px;
  height: 50px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
  vertical-align: middle;
  outline: none;
}
.navbar__hamburger:focus,
.navbar__hamburger:active {
  outline: none;
}
.navbar__search {
  display: inline-block;
}
.navbar__inputwithicon {
  position: relative;
  width: 300px;
}
.navbar__input {
  background-color: #34343f;
  border: 1px solid #34343f;
  width: 100%;
  height: 36px;
  border-radius: 18px;
  outline: none;
  padding: 0 30px 0 15px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #c0c7da;
  letter-spacing: 0.5px;
  word-spacing: 1px;
}
.navbar__input:focus {
  color: white;
}
.change-borderRadius {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.navbar__button {
  position: absolute;
  top: 3px;
  right: 3px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: #43cb83 url(/static/media/search_icon_pr2sma.79af4826.svg) no-repeat;
  background-size: 17px;
  background-position: center;
  cursor: pointer;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  will-change: background-color;
}
.navbar__button:hover,
.navbar__button:focus {
  background-color: #32b46f;
}
.navbar__button:active {
  background-color: #2fac6a;
  -webkit-transform: translateY(1px);
  transform: translateY(1px);
}
.navbar__toggler {
  display: none;
}
.navbar__links {
  list-style: none;
  padding: 0;
  /* margin: 0; */
  float: right;
}
.navbar__links li {
  cursor: pointer;
  position: relative;
  display: inline-block;
}
.navbar__links li:not(:last-child) a {
  padding: 0 15px;
}
.navbar__links li:last-child a {
  padding: 0 15px 0 15px;
}
.navbar__links li a {
  display: block;
  color: #c0c7da;
  text-decoration: none;
  font-family: inherit;
  font-size: 16px;
  -webkit-transition: color 200ms linear;
  transition: color 200ms linear;
  will-change: color;
}
