.stocks-landing-container {
  margin: 20px;
}
.stocks-landing-navbar {
  display: flex;
  -ms-flex-align: start;
  margin: 0 20px;
}
.stocks-landing-navbar > div {
  display: inline-block;
  margin: 20px 0;
  padding: 10px 6px;
  border: 1px solid var(--blue-navbar-background);
  color: white;
  font-size: 14px;
  cursor: pointer;
}
.onclick-bg-color {
  background-color: var(--blue-navbar-background);
}

@media (min-width: 325px) {
  .stocks-landing-navbar > div {
    padding: 10px 10px;
    font-size: 15px;
  }
}
@media (min-width: 480px) {
  .stocks-landing-navbar > div {
    font-size: 15px;
  }
}

.market-movers-container,
.sector-industry-container {
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  margin: 0 15px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: 13px;
}

.market-movers-box,
.sector-industry-box {
  justify-content: space-between;
  margin: 0 10px 50px 10px;
}
.market-movers-options,
.sector-industry-options {
  display: flex;
  color: white;
}
.market-movers-options > div,
.sector-industry-options > div {
  margin: 0 10px;
}

.market-movers-options > div:hover,
.sector-industry-options > div:hover {
  color: var(--blue-navbar-background);
  border-bottom: 1.5px solid var(--blue-navbar-background);
  padding-bottom: 5px;
}
.onclick-active-color {
  color: var(--blue-navbar-background);
  border-bottom: 1.5px solid var(--blue-navbar-background);
  padding-bottom: 5px;
}
.stocks_table__container {
  max-width: 400px;
}
.no-result-found {
  color: white;
  margin: 10px 50px 0 0;
  padding: 50px;
}
.loader-position {
  height: 300px;
  margin: 0 100px;
  display: flex;
  direction: column;
  justify-content: center;
  align-items: center;
}

/* Css for Search */

.stocks-main-search-wrapper {
  margin: 0 1px;
  float: right;
}
.stocks-search-container {
  display: flex;
  flex-wrap: nowrap;
  background-color: #3f3f4d;
  padding: 2px;
  border-radius: 8px;
  height: 25px;
  margin: 10px;
  max-width: 170px;
}
.stocks-search-container .stocks-search-button {
  height: 20px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: var(--pink-color) url(/static/media/search_icon_pr2sma.79af4826.svg)
    no-repeat;
  background-size: 17px;
  background-position: center;
  cursor: pointer;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  will-change: background-color;
}

.stocks-search-container .stocks-search-button:active {
  height: 23px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: palevioletred url(/static/media/search_icon_pr2sma.79af4826.svg) no-repeat;
  background-size: 17px;
  background-position: center;
  cursor: pointer;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  will-change: background-color;
}
.stocks-search-container .stocks-search-input {
  background-color: #3f3f4d;
  border: none;
  outline: none;
  height: 23px;
  width: 150px;
  border-radius: 8px;
  padding: 5px;
  color: white;
}

@media (min-width: 480px) {
  .stocks-search-container .stocks-search-button {
    height: 20px;
    width: 20px;
  }
  .market-movers-box,
  .sector-industry-box {
    height: 27px;
    display: flex;
    justify-content: space-between;
    margin: 15px 10px;
  }
  .market-movers-container,
  .sector-industry-container {
    font-size: 14px;
  }
  .stocks-search-container {
    margin: 0;
    max-width: 100%;
  }

  .stocks-search-container .stocks-search-input {
    padding: 8px;
  }
}

.pagination-on-stocks {
  list-style-type: none;
}
.pagination-on-stocks ul {
  overflow: hidden;
  padding: 10px;
  margin: 0;
}
.pagination-on-stocks a {
  border: 1px solid white;
  text-align: center;
  color: white;
  display: block;
  float: left;
  padding: 10px 5px;

  text-decoration: none;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
}
.pagination-on-stocks a::-moz-selection {
  background-color: var(--blue-navbar-background);
}
.pagination-on-stocks a:hover,
.pagination-on-stocks a::selection,
.pagination-active-page {
  background-color: var(--blue-navbar-background);
}

@media (min-width: 375px) {
  .pagination-on-stocks a {
    padding: 10px 7px;
  }
}
@media (min-width: 425px) {
  .pagination-on-stocks a {
    padding: 10px 9px;
  }
}
@media (min-width: 480px) {
  .pagination-on-stocks a {
    padding: 7px 14px;
  }
}

.companies__result {
  width: 95%;
  margin: 20px auto;
  background-color: #1c2030;
  height: 50px;
  border-radius: 8px;
}
.scrollable_div {
  height: 500px;
  overflow-y: scroll;
}
.MaxContainer {
  max-width: 1000px;
  margin: 0px auto;
}
.go__button {
  border: 1px solid #008542;
  background-color: #008542;
  width: 80px;
  margin-left: 20px;
  color: white;
  cursor: pointer;
}
.error__message {
  font-size: 20px;
  margin: 20px 0px;
  color: white;
}
.value__company {
  float: left;
  margin-left: 20px;
  padding: 15px;
  color: white;
}
.dropdown__advance {
  float: right;
  margin-right: 20px;
  color: white;
}
.result__card__grid {
  margin-left: 10px;
  display: grid;
  grid-gap: 10px;
  grid-template-rows: auto, auto, auto;
  grid-template-areas: "card card card card";
}
.result__card {
  grid: card;
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 160px;
  margin: 10px;
  box-shadow: 0 4px 6px 0;
  border-radius: 8px;
}
.result__card .company__name_parent {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
  line-height: 1px;
}
.company__logo {
  flex-grow: 0;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
}
.company__name {
  flex-grow: 4;
}
.company__name p {
  text-align: left;
  margin-left: 10px;
}
.result__card .values {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
}
.company__stats {
  flex-grow: 4;
  line-height: 1px;
}
.company__button {
  flex-grow: 2;
}
.company__button button {
  border: 1px solid var(--pink-color);
  background: transparent;
  width: 90%;
  padding: 10px;
  color: white;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 20px;
}
.company__button button:hover {
  background-color: var(--pink-color);
}

@media (max-width: 425px) {
  .value__company {
    font-size: 12px;
  }
  .scrollable__result {
    margin-top: 20px;
    height: 200px;
    overflow-y: scroll;
  }
  .maxContainer {
    max-width: 100%;
  }
  .result__card__grid {
    margin-left: auto;
    width: 90%;
    grid-template-areas:
      "card"
      "card"
      "card";
    /* grid-template-columns: auto, auto, auto; */
  }
}

