.companies__result {
  width: 95%;
  margin: 20px auto;
  background-color: #1c2030;
  height: 50px;
  border-radius: 8px;
}
.scrollable_div {
  height: 500px;
  overflow-y: scroll;
}
.MaxContainer {
  max-width: 1000px;
  margin: 0px auto;
}
.go__button {
  border: 1px solid #008542;
  background-color: #008542;
  width: 80px;
  margin-left: 20px;
  color: white;
  cursor: pointer;
}
.error__message {
  font-size: 20px;
  margin: 20px 0px;
  color: white;
}
.value__company {
  float: left;
  margin-left: 20px;
  padding: 15px;
  color: white;
}
.dropdown__advance {
  float: right;
  margin-right: 20px;
  color: white;
}
.result__card__grid {
  margin-left: 10px;
  display: grid;
  grid-gap: 10px;
  grid-template-rows: auto, auto, auto;
  grid-template-areas: "card card card card";
}
.result__card {
  grid: card;
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 160px;
  margin: 10px;
  box-shadow: 0 4px 6px 0;
  border-radius: 8px;
}
.result__card .company__name_parent {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
  line-height: 1px;
}
.company__logo {
  flex-grow: 0;
  height: -moz-fit-content;
  height: fit-content;
}
.company__name {
  flex-grow: 4;
}
.company__name p {
  text-align: left;
  margin-left: 10px;
}
.result__card .values {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
}
.company__stats {
  flex-grow: 4;
  line-height: 1px;
}
.company__button {
  flex-grow: 2;
}
.company__button button {
  border: 1px solid var(--pink-color);
  background: transparent;
  width: 90%;
  padding: 10px;
  color: white;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 20px;
}
.company__button button:hover {
  background-color: var(--pink-color);
}

@media (max-width: 425px) {
  .value__company {
    font-size: 12px;
  }
  .scrollable__result {
    margin-top: 20px;
    height: 200px;
    overflow-y: scroll;
  }
  .maxContainer {
    max-width: 100%;
  }
  .result__card__grid {
    margin-left: auto;
    width: 90%;
    grid-template-areas:
      "card"
      "card"
      "card";
    /* grid-template-columns: auto, auto, auto; */
  }
}
