.dropdown-container {
  /* float: right; */
  /* margin: 0 10px; */
  margin-left: 80vw;
  position: relative;
  display: flex;
  flex-direction: row;
}
.dropdown__parent {
  display: flex;
  width: 100%;
  margin-top: 40px;
}
.reports-dropdown {
  border-radius: 8px;
  width: 100px;
  cursor: pointer;
  padding: 5px 20px;
  color: var(--pink-color);
  border: 1px solid var(--pink-color);
}
i {
  border: solid var(--pink-color);
  border-width: 0 3px 3px 0;
  display: inline-block;
  /* margin-top: calc(100% - 10px) 50%; */
  margin: 3px 4px;
  padding: 3px;
}
.droparrow {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.reports-dropdown:active,
.reports-dropdown:focus {
  outline: none;
}
/* .reports-dropdown:hover {
  background: var(--pink-color);
  color: white;
} */

.dropdown-container:hover .reports-year-option {
  display: block;
  margin-left: 80vw;
}
.reports-year-option ul {
  padding: 0px;
}
.reports-year-option li {
  list-style-type: none;
  padding: 3px 2px;
  text-align: center;
}
.showDisplay {
  display: block;
  width: 100px;
  border: 1px solid var(--table-header-background);
  background: var(--table-header-background);
  color: var(--pink-color);
  right: 0px;
  top: 35px;
}
.showDisplay li {
  list-style-type: none;
  padding: 3px 2px;
  text-align: center;
}
.showDisplay ul {
  padding: 0px;
}
.noDisplay {
  display: none;
}
.reports-year-option {
  display: none;
  width: 100px;
  border: 1px solid var(--table-header-background);
  background: var(--table-header-background);
  color: var(--pink-color);
}
.Reports {
  height: 600px;
  overflow-y: scroll;
  margin-top: 20px;
}
.Reports h2 {
  color: var(--pink-color);
  text-align: left;
  margin-top: 20px;
  margin-left: 7vw;
  margin-bottom: 0px;
  font-size: 40px;
}

@media (max-width: 425px) {
  .Reports h2 {
    font-size: 30px;
    margin-bottom: 5px;
    margin-left: 5vw;
  }
  .dropdown-container {
    margin-left: 60vw;
  }
}
