.pagination-on-stocks {
  list-style-type: none;
}
.pagination-on-stocks ul {
  overflow: hidden;
  padding: 10px;
  margin: 0;
}
.pagination-on-stocks a {
  border: 1px solid white;
  text-align: center;
  color: white;
  display: block;
  float: left;
  padding: 10px 5px;

  text-decoration: none;
  transition: background-color 0.3s;
}
.pagination-on-stocks a:hover,
.pagination-on-stocks a::selection,
.pagination-active-page {
  background-color: var(--blue-navbar-background);
}

@media (min-width: 375px) {
  .pagination-on-stocks a {
    padding: 10px 7px;
  }
}
@media (min-width: 425px) {
  .pagination-on-stocks a {
    padding: 10px 9px;
  }
}
@media (min-width: 480px) {
  .pagination-on-stocks a {
    padding: 7px 14px;
  }
}
