@media screen and (max-width: 480px) {
  .dash {
    display: grid;
    grid-template-areas:
      "ratio ratio ratio ratio"
      "piechart piechart piechart piechart"
      "closing closing closing closing"
      "gauge gauge gauge gauge"
      "market market market market"
      "monte monte monte monte";
    /* grid-template-columns: repeat(12, 1fr); */
    grid-gap: 15px;
    grid-template-rows: 150px repeat(5, 350px);
    /* background-color: blue; */
  }

  body {
    background-color: white;
    /* margin-top: 3%; */
  }

  .dashboard__container {
    margin: 2% 4%;
  }

  .ratios {
    /* grid-row: 1/1; */
    grid-area: ratio;
    display: grid;
    grid-template-columns: repeat(4, 1fr);

    /* grid-gap: 10px; */
    /* font-size: 12px */
    /* font-weight: 600; */
    color: #fff;
    justify-content: center;
  }
  .ratios p:first-child {
    /* font-size: 14px; */
    /* font-weight: 200; */
    color: var(--pink-color);
  }
  .ratios p:nth-child(2) {
    /* font-size: 25px; */
    /* font-weight: 600; */
    color: var(--blue-color);
  }
  .ratios div {
    border-bottom: 10px solid #f3ca3e;
    background-color: var(--card-background);
    /* box-shadow: 3px 3px 5px 6px #1f2435;
 */
    box-shadow: 0 4px 6px 0 #1f2435;
    max-width: 90px;
  }

  .ratio1 {
    /* grid-column: 1/1; */
    /* grid-area: ratio1; */
    /* background-color: blueviolet; */
  }
  .ratio2 {
    /* grid-column: 2/2; */
    /* grid-area: ratio2; */
    /* background-color: blueviolet; */
  }

  .ratio3 {
    /* grid-column: 3/3; */
    /* grid-area: ratio3; */
    /* background-color: blueviolet; */
  }

  .ratio4 {
    /* grid-column: 4/4; */
    /* grid-area: ratio4; */
    /* background-color: blueviolet; */
  }

  .piechart {
    grid-area: piechart;
    background-color: var(--page-background) !important;

    box-shadow: 0 4px 6px 0;
  }

  .closing {
    background-color: var(--page-background);

    box-shadow: 0 4px 6px 0;
    grid-area: closing;
  }

  .gauge {
    box-shadow: 0 4px 6px 0;
    grid-area: gauge;
  }

  .market-cap {
    grid-area: market;

    background-color: var(--page-background);

    box-shadow: 0 4px 6px 0;
  }

  .monte-carlo {
    grid-area: monte;

    border-radius: 8px;

    background-color: var(--page-background);
  }

  .monte-carlo body {
    box-shadow: 0 4px 6px 0;
  }
}

@media screen and (min-width: 480px) {
  .dash {
    display: grid;
    grid-template-rows: 140px 10px 300px 100px 400px 300px;
    /* grid-template-rows: auto auto auto auto auto auto; */
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 15px;
    /* background-color: blue; */
  }

  body {
    background-color: white;
    /* margin-top: 3%; */
  }

  .dashboard__container {
    margin: 2% 4%;
  }

  .ratios {
    grid-row: 1/1;
    /* background-color: yellow; */
    grid-column: 1/8;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    /* font-size: 30px;
  font-weight: 600; */
    color: #fff;
    justify-content: center;
  }
  .ratios p:first-child {
    font-size: 20px;
    font-weight: 200;
    color: var(--pink-color);
  }
  .ratios p:nth-child(2) {
    font-size: 25px;
    font-weight: 600;
