    /* border: 1px solid white; */
    background-color: var(--card-background);
    /* box-shadow: 3px 3px 5px 6px #1f2435; */
    box-shadow: 0 4px 6px 0;
  }

  .gauge {
    grid-row: 4/6;
    grid-column: 8/-1;
    /* background-color: white; */
    /* border: 1px solid white; */
    background-color: var(--card-background);
    /* box-shadow: 3px 3px 5px 6px #1f2435; */
    box-shadow: 0 4px 6px 0;
  }

  .market-cap {
    grid-row: 5/6;
    grid-column: 1/8;
    /* border: 1px solid white; */
    background-color: var(--card-background);
    /* box-shadow: 3px 3px 5px 6px #1f2435; */
    box-shadow: 0 4px 6px 0;

    /* background-color: green; */
  }

  /* .incomePara {
    content: "a";
  } */

  .monte-carlo {
    /* background-color: orange; */
    grid-column: 1/-1;
    grid-row: 6/-1;
    /* color: #1c2030 !important; */
    border-radius: 8px;
    /* box-shadow: 0 8px 8px 0; */
    /* border: 1px solid white; */
    background-color: var(--card-background);
    /* box-shadow: 3px 3px 5px 6px #1f2435; */
    /* box-shadow: 0 4px 6px 0; */
    /* border: 1px solid white; */
    /* border: 1px solid white; */
  }

  .monte-carlo body {
    box-shadow: 0 4px 6px 0;
  }
}
