/* Dropdown button on hover & focus */
.dropbtn:hover,
.dropbtn:focus {
  background-color: var(--pink-color);
  outline: none;
}
.dropbtn:active {
  outline: none;
}
.dropbtn:hover .droparrow {
  border: solid white;
  border-width: 0 3px 3px 0;
  display: inline-block;
  margin: 3px 4px;
  padding: 3px;
}
/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: var(--page-background);
  min-width: 250px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-content:active,
.dropdown-content:focus {
  outline: none;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: white;
  padding: 5px;
  text-decoration: none;
  display: block;
}
.dropbtn {
  background-color: var(--page-background);
  border: 1px solid var(--pink-color);
  width: 100%;
  height: 25px;
  border-radius: 10px;
  outline: none;
  padding: 0 30px 0 15px;
  /* font-family: Roboto, sans-serif; */
  font-size: 14px;
  font-weight: 500;
  color: white;
  letter-spacing: 0.5px;
  word-spacing: 1px;
  cursor: pointer;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  background-color: var(--card-background);
}

/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.show {
  display: block;
}

body {
  background-color: var(--page-background);
}

.market__container {
  margin: 2% 4%;
  /* max-width: 320px; */
}

.market__grid {
  display: grid;
  grid-gap: 15px;
  /* margin-left: 5%;
        margin-right: 5%; */
  grid-template-rows: 350px 400px 20px repeat(2, 350px) 50px;
  grid-template-areas:
    "vis"
    "ticker"
    "sector"
    "gainer"
    "loser"
    "footer";
}

.navbar {
  background-color: black;
  grid-area: nav;
}

.sector {
  grid-area: sector;
}

.ticker-table {
  /* background-color: red; */
  grid-area: ticker;
  display: grid;
  /* margin-top: 15%; */

  justify-items: center;
  clear: both;
}

.ticker {
  box-shadow: 0 4px 6px 0;
}

.ticker button {
  /* display: inline; */
  float: left;
  margin: 8px;
  /* margin-left: 10px; */
  width: 10%;
  height: 10%;
  border-radius: 8px;
  background-color: var(--card-background);
  color: white;
  border: 1px solid var(--pink-color);
}

.ticker button:hover {
  background-color: var(--pink-color);
}

.vis {
  background-color: var(--card-background);
  grid-area: vis;
  border-radius: 8px;
  box-shadow: 0 4px 6px 0;
}
.gainer {
  background-color: var(--card-background);
  grid-area: gainer;

  box-shadow: 0 4px 6px 0;
}
.loser {
  background-color: var(--card-background);
  grid-area: loser;
  /* color: white; */
  box-shadow: 0 4px 6px 0;
}
.footer {
  background-color: black;
  grid-area: footer;
}

.ticker tr {
  background-color: var(--card-background);
  color: white;
}

.ticker tr:hover {
  cursor: pointer;
}

.ticker {
  background-color: var(--card-background);
}
tr:last-child {
  border-bottom: 2px solid #1c2030;
}

.markets-table {
  width: 95%;
  height: 80%;
  /* border: 1px solid #808080; */
  /* box-shadow: 0 4px 6px 0; */
  /* border: 0.5px solid #1c2030; */
  border-collapse: collapse;
  border-radius: 10px;
}

.markets-tableHeader {
  background-color: #1c2030;
  color: var(--blue-color);
  border-radius: 8px;
  width: 80%;
}

.markets-table td {
  padding: 5px;
  /* margin-left: 5px; */
  text-align: center;
}

.markets-table th {
  padding: 10px;
  background-color: var(--table-header-background);
  color: var(--blue-color);
}

.gainerLoader {
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 805px) {
  .market__grid {
    grid-template-rows: 350px 20px 280px 50px;
    /* grid-template-areas: 
        
        "ticker vis vis vis "
        "gainer gainer loser loser"
        "footer footer footer footer"
    } */
    grid-template-columns: repeat(12, 1fr);
  }
  .ticker {
    grid-column: 1/5;
    grid-row: 1/1;
  }
  .vis {
    grid-column: 5/-1;
    grid-row: 1/1;
  }

  .sector {
    grid-column: 11/-1;
    grid-row: 2/2;
  }
  .gainer {
    grid-column: 1/7;
    grid-row: 3/5;
  }
  .loser {
    grid-column: 7/-1;
    grid-row: 3/5;
  }
  /* .ticker-table {
    margin-top: 15%;
  } */
}
