.ticker__table tr:nth-child(odd) {
  background-color: var(--table-header-background);
}

.ticker__table {
  width: 100%;
  height: auto;
  border-collapse: collapse;
  border-radius: 10px;
}

.ticker__tableHeader {
  background-color: #1c2030;
  color: var(--blue-color);
  border-radius: 8px;
  width: 80%;
}

.ticker__table td {
  padding: 10px;
  text-align: center;
  color: white;
}

.ticker__table th {
  padding: 10px;
  /* background-color: var(--table-header-background); */
  color: var(--blue-color);
}
/* .ticker__table tr:nth-child(odd) {
  background-color: var(--table-header-background);
} */
.table__container {
  overflow-x: scroll;
}

@media (max-width: 480px) {
  .table__container {
    max-width: 100%;
    margin: 0px auto 10px auto;
  }

  .ticker__table th:first-child,
  .ticker__table td:first-child {
    /* top: auto; */
    position: sticky;
    left: 0;
    text-align: left;
    /* visibility: visible; */
  }
  .ticker__table td {
    word-break: keep-all;
    white-space: nowrap;
    word-wrap: none;
  }
  th {
    background-color: var(--table-header-background);
  }
  tr:nth-child(even) td:first-child {
    background-color: #1c2030;
  }
  tr:nth-child(odd) td:first-child {
    background-color: var(--table-header-background);
  }
}
