.market-movers-container,
.sector-industry-container {
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  margin: 0 15px;
  height: fit-content;
  font-size: 13px;
}

.market-movers-box,
.sector-industry-box {
  justify-content: space-between;
  margin: 0 10px 50px 10px;
}
.market-movers-options,
.sector-industry-options {
  display: flex;
  color: white;
}
.market-movers-options > div,
.sector-industry-options > div {
  margin: 0 10px;
}

.market-movers-options > div:hover,
.sector-industry-options > div:hover {
  color: var(--blue-navbar-background);
  border-bottom: 1.5px solid var(--blue-navbar-background);
  padding-bottom: 5px;
}
.onclick-active-color {
  color: var(--blue-navbar-background);
  border-bottom: 1.5px solid var(--blue-navbar-background);
  padding-bottom: 5px;
}
.stocks_table__container {
  max-width: 400px;
}
.no-result-found {
  color: white;
  margin: 10px 50px 0 0;
  padding: 50px;
}
.loader-position {
  height: 300px;
  margin: 0 100px;
  display: flex;
  direction: column;
  justify-content: center;
  align-items: center;
}

/* Css for Search */

.stocks-main-search-wrapper {
  margin: 0 1px;
  float: right;
}
.stocks-search-container {
  display: flex;
  flex-wrap: nowrap;
  background-color: #3f3f4d;
  padding: 2px;
  border-radius: 8px;
  height: 25px;
  margin: 10px;
  max-width: 170px;
}
.stocks-search-container .stocks-search-button {
  height: 20px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: var(--pink-color) url("../assets/search_icon_pr2sma.svg")
    no-repeat;
  background-size: 17px;
  background-position: center;
  cursor: pointer;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  transition: background-color 0.2s linear;
  will-change: background-color;
}

.stocks-search-container .stocks-search-button:active {
  height: 23px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: palevioletred url("../assets/search_icon_pr2sma.svg") no-repeat;
  background-size: 17px;
  background-position: center;
  cursor: pointer;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  transition: background-color 0.2s linear;
  will-change: background-color;
}
.stocks-search-container .stocks-search-input {
  background-color: #3f3f4d;
  border: none;
  outline: none;
  height: 23px;
  width: 150px;
  border-radius: 8px;
  padding: 5px;
  color: white;
}

@media (min-width: 480px) {
  .stocks-search-container .stocks-search-button {
    height: 20px;
    width: 20px;
  }
  .market-movers-box,
  .sector-industry-box {
    height: 27px;
    display: flex;
    justify-content: space-between;
    margin: 15px 10px;
  }
  .market-movers-container,
  .sector-industry-container {
    font-size: 14px;
  }
  .stocks-search-container {
    margin: 0;
    max-width: 100%;
  }

  .stocks-search-container .stocks-search-input {
    padding: 8px;
  }
}
