.stocks-landing-container {
  margin: 20px;
}
.stocks-landing-navbar {
  display: flex;
  -ms-flex-align: start;
  margin: 0 20px;
}
.stocks-landing-navbar > div {
  display: inline-block;
  margin: 20px 0;
  padding: 10px 6px;
  border: 1px solid var(--blue-navbar-background);
  color: white;
  font-size: 14px;
  cursor: pointer;
}
.onclick-bg-color {
  background-color: var(--blue-navbar-background);
}

@media (min-width: 325px) {
  .stocks-landing-navbar > div {
    padding: 10px 10px;
    font-size: 15px;
  }
}
@media (min-width: 480px) {
  .stocks-landing-navbar > div {
    font-size: 15px;
  }
}
