.main-search-wrapper {
  float: right;
  margin: 10px;
}
.search-container {
  border: 1px solid var(--pink-color);
  display: flex;
  flex-wrap: nowrap;
  background-color: transparent;
  padding: 2px;

  border-radius: 8px;
}
.search-container .search-button {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: var(--pink-color) url("../assets/search_icon_pr2sma.svg")
    no-repeat;
  background-size: 17px;
  background-position: center;
  cursor: pointer;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  transition: background-color 0.2s linear;
  will-change: background-color;
}

.search-container .search-button:active {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: palevioletred url("../assets/search_icon_pr2sma.svg") no-repeat;
  background-size: 17px;
  background-position: center;
  cursor: pointer;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  transition: background-color 0.2s linear;
  will-change: background-color;
}
.search-container .search-input {
  background-color: transparent;
  border: none;
  outline: none;
  height: 30px;
  width: 150px;
  border-radius: 8px;
  padding: 5px;
  color: white;
}

@media only screen and (max-width: 768px) {
  .main-search-wrapper {
    margin-top: 10px;
    z-index: 1;
  }
}
@media only screen and (max-width: 320px) {
  .main-search-wrapper {
    margin-top: 10px;
    z-index: 1;
  }
}
