.grid-container-for-reusable-card {
  display: grid;
  grid-area: card;

  grid-template-areas: "card card card";
  grid-gap: 2px;
  padding: 20px;
  float: left;
  height: 500px;
  margin-top: 25px;
  overflow: scroll;
}
.main-card-container {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 160px;
  margin: 10px;
  box-shadow: 0 4px 6px 0;
  border-radius: 8px;
}
.co-name {
  color: var(--blue-color);
  font-size: 24px;
  margin: 35px 146px 0px 0px;
}
#border-point-effect {
  border-left: solid white;
}

.co-tickers {
  color: var(--blue-color);
  margin: 20px 158px 0px 0px;
}
.logo-image {
  float: left;
  margin: 10px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.main-card-container .flex-company-name {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
  line-height: 1px;
}
.flex-company-name .company-logo {
  flex-grow: 0;
  height: fit-content;
}
.flex-company-name .company-name {
  flex-grow: 4;
}

.main-card-container .flex-company-values {
  flex-grow: 5;
  display: flex;
  flex-direction: row;
}

.flex-company-values .company-stats {
  flex-grow: 4;
  line-height: 1px;
}
.company-stats .stock-price {
  color: white;
  font-size: 20px;
  text-align: left;
  margin-left: 10px;
}
.company-stats .price-on-date {
  color: white;
  font-size: 12px;
  margin: 0px 114px 0px 0px;
}
.flex-company-values .company-unfollow {
  flex-grow: 2;
}
.company-unfollow .unfollow-button {
  width: 70px;
  height: 30px;
  margin-top: 20px;
  border: 1px solid var(--pink-color);
  outline: none;
  background-color: var(--card-background);
  border-radius: 4px;
  color: var(--pink-color);
}

.company-unfollow .unfollow-button:active {
  width: 70px;
  height: 30px;
  margin-top: 20px;
  border: 1px solid var(--pink-color);
  outline: none;
  background-color: var(--pink-color);
  border-radius: 4px;
  color: white;
}

@media only screen and (max-width: 768px) {
  .grid-container-for-reusable-card {
    position: absolute;
    top: 150px;
    left: 10px;
    display: grid;
    grid-area: card;

    grid-template-areas: "card card";
    grid-gap: 20px;
    padding: 20px;
  }
}

@media only screen and (max-width: 425px) {
  .grid-container-for-reusable-card {
    position: absolute;
    left: 55px;
    top: 170px;
    display: grid;
    grid-area: card;

    grid-template-areas: " card";
    grid-gap: 10px;
    padding: 10px;
    z-index: 0;
  }

  .main-card-container {
    display: flex;
    flex-direction: column;
    width: 250px;
    height: 160px;
    margin: 10px;
    box-shadow: 0 4px 6px 0;
    border-radius: 8px;
  }
  .company-stats .stock-price {
    color: white;
    font-size: 17px;
    text-align: left;
    margin-left: 10px;
    margin-top: 31px;
  }
  .company-stats .price-on-date {
    display: none;
  }

  .company-unfollow .unfollow-button {
    width: 70px;
    height: 30px;
    margin-top: 20px;
    margin-right: 10px;
    border: 1px solid var(--pink-color);
    outline: none;
    background-color: var(--card-background);
    border-radius: 4px;
    color: var(--pink-color);
  }
}

