.check-icon {
  color: var(--green-color);
  position: absolute;
  width: 100%;
  height: 40px;
  float: right;
}
body {
  background-color: #1c2030;
}

.login-box {
  position: relative;
  top: 10px;
  margin: 10px auto;
  width: 600px;
  height: 500px;
  background-color: var(--card-background);
  padding: 10px;
  border-radius: 3px;
}
.lb-header {
  position: relative;
  color: #00415d;
  margin: 5px 5px 10px 5px;
  padding-bottom: 10px;
  text-align: center;
  height: 28px;
}
.lb-header a {
  margin: 0 25px;
  padding: 0 20px;
  text-decoration: none;
  color: #666;
  font-weight: bold;
  font-size: 15px;
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  transition: all 0.1s linear;
}

#login-box-link,
#signup-box-link {
  font-size: 25px;
  color: white;
  margin: 5px;
  background-color: var(--card-background);
  border: none;
  transition: 0.3s ease-in;
}
#login-box-link1,
#signup-box-link1 {
  font-size: 25px;
  color: white;
  margin: 5px;
  background-color: var(--card-background);
  outline: none;
  border: none;
  border-bottom: 4px solid var(--blue-color);
  transition: 0.3s ease-in;
}
#login-box-link:active,
#signup-box-link:active {
  border: none;
  outline: none;
  cursor: pointer;
}
#login-box-link1:focus,
#signup-box-link1:focus {
  outline: none;
  cursor: pointer;
}
#login-box-link1:hover,
#signup-box-link:hover {
  outline: none;
  cursor: pointer;
}
.social-login {
  position: relative;
  float: left;
  width: 100%;
  height: auto;
  padding: 10px 0 15px 0;
}
.social-login a {
  position: relative;
  float: left;
  width: calc(40% - 8px);
  text-decoration: none;
  color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.05);
  padding: 12px;
  border-radius: 2px;
  font-size: 12px;
  margin: 0 3%;
  text-align: center;
}
.social-login a i {
  position: relative;
  float: left;
  width: 20px;
  top: 2px;
}
.social-login a:first-child {
  background-color: #49639f;
}
.social-login a:last-child {
  background-color: #df4a32;
}
.email-login,
.email-signup {
  position: relative;
  float: left;
  width: 100%;
  height: auto;
  margin-top: 20px;
  text-align: center;
  border: none;
}
.u-form-group {
  width: 100%;
  margin-bottom: 35px;
}

.u-form-group input[type="email"],
.u-form-group input[type="password"],
.u-form-group input[type="text"] {
  letter-spacing: 1px;

  width: calc(50% - 22px);
  height: 45px;
  background-color: var(--card-background);
  outline: none;
  border: none;
  border-bottom: 1px solid white;
  padding-bottom: 0px;
  border-radius: 2px;
  color: white;
  font-size: 15px;
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  transition: all 0.1s linear;
}
