.u-form-group button {
  width: 50%;
  background-color: var(--card-background);

  border: 1px solid var(--blue-color);
  outline: none;
  color: var(--blue-color);
  font-size: 14px;
  font-weight: normal;
  padding: 14px 0;
  border-radius: 2px;
}
.u-form-group button:active {
  background-color: var(--blue-color);
  color: white;
  transition: 0.3 ease-in;
}
